<template>
	<view>
		<!-- 自定义导航栏 -->
		<uni-nav-bar :statusBar="true" rightText="发布" left-icon="arrowleft"  @clickLeft="back" @clickRight="submit">
			<view @tap="changelook"  class="u-f-ajc">{{yinsi}}<view class="icon iconfont icon-xialazhankai"></view></view>
		</uni-nav-bar>
		<!-- 多行输入框 -->
		<view class="uni-textarea">
			<textarea v-model="text" placeholder="说一句话吧~" />
		</view>
		<!-- 上传多图功能 -->
		<upload-image @upload="upload"></upload-image>
		<!-- 弹出公告 -->
		<uni-popup :show="showpopup" position="middle" mode="fixed" @hidePopup="hidePopup">
			11111
		</uni-popup>
	</view>
</template>

<script>
	
	let changelook=['所有人可见','仅自己可见']
	import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
	import uploadImage from "../../components/common/upload-image.vue";
	import uniPopup from "../../components/uni-popup/uni-popup.vue";
	export default {
		components:{
			uniNavBar,
			uploadImage,
			uniPopup
		},
		data() {
			return {
				showpopup:true,
				yinsi:"所有人可见",
				text:'',
				imglist:[]
			}
		},
		methods: {
			upload(arr){
				this.imglist=arr;
			},
			// 返回
			back(){
				uni.navigateBack({
					delta:1
				})
			},
			// 发布
			submit(){
				console.log("发布")
			},
			// 隐私
			changelook(){
				uni.showActionSheet({
					itemList:changelook,  //按钮上的文字数组
					success:(res)=> {
						this.yinsi=changelook[res.tapIndex];
					}
				})
			},
			hidePopup(){
				this.showpopup=false;
			}
		}
	}
</script>

<style>
	.uni-textarea{
		border: 1upx solid #eee;
	}
	
	
</style>
